import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {LighthouseBrandListDisplayItem} from '../models/lighthouse/lighthouse-source-search';
import {OrganizationConnectionCallback} from '../models/organization-connection-callback';

@Injectable({
  providedIn: 'root'
})
export class MessageBusService {

  //this subject is populated when a Brand is selected. It will trigger the display of the PortalConnectComponent
  public selectedBrandSubject = new BehaviorSubject<LighthouseBrandListDisplayItem | null>(null)

  //this subject is populated when a Portal/Endpoint is selected. It is used to display endpoint information when doing popup mode.
  public orgConnectionCallbackSubject = new BehaviorSubject<OrganizationConnectionCallback | null>(null)

  constructor() { }

  reset(){
    this.selectedBrandSubject.next(null)
    this.orgConnectionCallbackSubject.next(null)
  }
}
