import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {initFlowbite, Modal} from 'flowbite';
import {LighthouseBrandListDisplayItem} from '../models/lighthouse/lighthouse-source-search';
import {ApiMode, ConnectMode} from '../constants';
import {Organization} from '../models/organization';
import {ConnectService} from '../services/connect.service';
import {MessageBusService} from '../services/message-bus.service';
import {OrganizationConnectionCallback} from '../models/organization-connection-callback';
import { ElementRef, ViewChild } from '@angular/core';

export class SourceListItem {
  brand: LighthouseBrandListDisplayItem
  searchHighlights?: string[]
}


@Component({
  // standalone: true,
  selector: 'fasten-stitch',
  templateUrl: './fasten-stitch.component.html',
  styleUrls: ['./fasten-stitch.component.css'],
  // SEE https://stackoverflow.com/questions/67115155/angular-web-component-how-to-embed-css-file-into-the-final-js-file
  // using view encapsulation to namespace CSS.
  encapsulation: ViewEncapsulation.Emulated //default is  ViewEncapsulation.Emulated
})
export class FastenStitchComponent implements OnInit {
  @Input('public-id') publicId = ''; //validate
  @Input('connect-mode') connectMode: ConnectMode = ConnectMode.Redirect;
  @Input('reconnect-org-connection-id') reconnectOrgConnectionId: string | null = null;

  @Output('orgConnectionCallback') orgConnectionCallback = new EventEmitter<OrganizationConnectionCallback>()

  public apiMode: ApiMode = ApiMode.Test
  public org: Organization | undefined

  public errorMessage: string

  @ViewChild('stitchModal', { static: false }) public stitchModal: ElementRef;

  constructor(
    private connectApi: ConnectService,
    public messageBus: MessageBusService,
  ) { }

  ngOnInit(): void {
    initFlowbite();

    let publicIdParts = this.publicId.split('_')
    if(publicIdParts.length == 3 && publicIdParts[1] == ApiMode.Live) {
      this.apiMode = ApiMode.Live
    }
    if(publicIdParts.length != 3){
      console.error("Could not register Fasten Connect installation: missing or invalid id", this.publicId)
      this.errorMessage = 'Could not register Fasten Connect installation: missing or invalid id. Please contact the developer of this app.'
      return
    }
    console.log(this.publicId, publicIdParts, this.apiMode)

    this.connectApi.getOrgByPublicId(this.publicId).subscribe((org: Organization) => {
      this.org = org
    },(err) => {
      this.errorMessage = 'Could not register Fasten Connect installation using id. Please contact the developer of this app.'
      console.log("Invalid Fasten Connect registration", err)
    })

    this.messageBus.orgConnectionCallbackSubject.subscribe((orgConnectionCallbackData) => {
      console.log("bubbling up event", orgConnectionCallbackData)
      this.orgConnectionCallback.emit(orgConnectionCallbackData)
    })
  }

  showStitchModal(el): void {
    const modal = new Modal(this.stitchModal.nativeElement);
    modal.show()
  }

  hideStitchModal(el): void {
    const modal = new Modal(this.stitchModal.nativeElement);
    modal.hide()
    this.messageBus.reset()
  }
}
